.categories {
  width: 356px;
}

.categories-list {
  width: 100%;
  height: 472px;
  flex-direction: column;
  gap: 32px;
  list-style: none;
  display: flex;
  overflow-x: hidden;
  overflow-y: auto;
}

.categories-list-item {
  height: 24px;
  color: rgba(17, 17, 17, .6);
  letter-spacing: -.36px;
  cursor: pointer;
  padding-right: 20px;
  font-family: DM Sans;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.33;
  transition: color .2s ease-in, text-transform .2s ease-in, font-weight .2s ease-in;
}

.categories-link {
  text-decoration: none;
}

.category-active, .categories-link:is(:hover, :focus) .categories-list-item {
  text-transform: uppercase;
  color: #4f2ee8;
  font-weight: 700;
}

.categories-list::-webkit-scrollbar {
  width: 8px;
  background-color: rgba(0, 0, 0, 0);
}

.categories-list::-webkit-scrollbar-thumb {
  height: 167px;
  background-color: #e0e0e0;
  border-radius: 12px;
}

